@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600);
body {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 13px; }

a {
  color: #000000; }
  a.no-decoration {
    text-decoration: none; }
  a:hover {
    color: #000000; }
  a:visited {
    color: #828282; }

strong {
  font-weight: 600; }

img {
  vertical-align: top; }

.app {
  margin-top: 8px;
  margin-bottom: 8px; }

.text-light {
  font-size: 12px;
  color: #828282 !important; }

.page-nav {
  margin-left: 20px; }
  .page-nav-item {
    font-size: 12px;
    color: #828282 !important; }
    .page-nav-item:last-child:not(:only-child) {
      margin-left: 10px; }
  .page-nav:hover {
    text-decoration: none; }

nav.navbar {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background-color: #ff6600;
  padding: 0.2rem;
  padding-right: 1rem; }
  nav.navbar .logo {
    color: #000000;
    text-decoration: none; }
    nav.navbar .logo:hover, nav.navbar .logo:visited {
      color: #000000;
      text-decoration: none; }
    nav.navbar .logo .logo-img {
      border: 1px solid white;
      margin-right: 5px; }

.layout {
  padding: 10px 15px;
  background-color: #F6F6EF; }

.story {
  margin-bottom: 8px;
  display: -webkit-flex;
  display: flex; }
  .story-title {
    color: #000000; }
  .story-body {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin-left: 3px; }
  .story-index {
    color: #828282; }
  .story-author a,
  .story-comments a {
    color: #828282; }
    .story-author a:hover,
    .story-comments a:hover {
      -webkit-text-decoration: 1px solid #828282;
              text-decoration: 1px solid #828282; }
  .story .separator {
    margin: 0 3px; }
  .story .no-margin {
    margin: 0 !important; }
  .story .back-button {
    font-size: 10px; }
    .story .back-button:hover {
      text-decoration: underline; }
  .story-info {
    color: #828282;
    font-size: 10px;
    display: -webkit-flex;
    display: flex; }
    .story-info > div {
      margin-right: 5px; }
      .story-info > div span:last-child {
        margin-left: 3px; }

.post-comments {
  margin-top: 20px; }

.comment {
  margin-bottom: 10px; }
  .comment .deleted {
    color: #9c9c9c; }
  .comment-date {
    margin-left: 5px; }
  .comment-info {
    color: #828282;
    margin-bottom: 3px;
    font-size: 11px; }
    .comment-info .comments-toggle {
      margin-left: 5px; }
    .comment-info > a {
      color: #828282; }
    .comment-info > span:last-child {
      margin-left: 5px; }
  .comment .nested-comments {
    margin-top: 20px;
    margin-left: 25px; }
    @media (max-width: 992px) {
      .comment .nested-comments {
        margin-left: 15px; } }
    @media (max-width: 768px) {
      .comment .nested-comments {
        margin-left: 10px; } }

.link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: none;
  display: inline;
  margin: 0;
  padding: 0; }
  .link-button:focus, .link-button:active {
    box-shadow: none;
    border: none;
    outline: none; }

.link-button:hover,
.link-button:focus {
  text-decoration: none; }

.loading {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }
  .loading-icon {
    margin-right: 10px;
    max-width: 10px; }
  .loading-text {
    font-size: 12px;
    color: #828282 !important; }

footer {
  border-top: 1px solid #ff6600;
  padding-top: 10px;
  margin-top: 10px;
  font-size: 10px;
  color: #828282; }

